"use client";
import React, { useState, useEffect } from "react";
import { Menu, X } from "lucide-react";
import Link from 'next/link'
const Header = () => {
  const [isScrolled, setIsScrolled] = useState(false);
  const [isMobileMenuOpen, setIsMobileMenuOpen] = useState(false);
  const [activePath, setActivePath] = useState("/about");

  useEffect(() => {
    const handleScroll = () => {
      setIsScrolled(window.scrollY > 50);
    };
    window.addEventListener("scroll", handleScroll);
    return () => window.removeEventListener("scroll", handleScroll);
  }, []);

  const navLinks = [
    { name: "Our Ink & Passion", href: "/about" },
    { name: "Our Capabilities", href: "/services" },
    { name: "The Proof Gallery", href: "/portfolio" },
  ];

  return (
    <nav
      className={`fixed w-full z-50 transition-all duration-300 ${
        isScrolled
          ? "backdrop-blur-sm bg-white/30 py-4"
          : "bg-transparent py-6 text-neutral-900"
      }`}
    >
      <div className="max-w-11/12 mx-auto px-4 sm:px-6 lg:px-8">
        <div className="flex justify-between items-center">
          <div className="flex items-center space-x-2">
            <div>
              <Link
                href="/"
                className={`text-xs ${
                  isScrolled ? "text-gray-600" : "text-gray-200"
                }`}
              >
                LOGO
              </Link>
            </div>
          </div>

          {/* Desktop Navigation */}
          <div className="hidden md:flex space-x-8">
            {navLinks.map((link) => {
              const isActive = activePath === link.href;
              return (
                <Link
                  key={link.name}
                  href={link.href}
                  onClick={(e) => {
                    e.preventDefault();
                    setActivePath(link.href);
                  }}
                  className={`relative font-medium text-xl transition-colors group ${
                    isScrolled
                      ? isActive
                        ? "text-blue-600"
                        : "text-gray-700"
                      : isActive
                      ? "text-white"
                      : "text-white"
                  }`}
                >
                  {link.name}

                  {/* Vibrating underline with rounded edges */}
                  <span
                    className={`absolute left-0 -bottom-1 w-full h-[4px] rounded-full transition-all duration-300 ${
                      isScrolled ? "bg-blue-600" : "bg-white"
                    } ${
                      isActive ? "opacity-100" : "opacity-0 group-hover:opacity-100"
                    }`}
                    style={{
                      animation: isActive
                        ? "none"
                        : undefined,
                    }}
                  />

                  <style jsx>{`
                    a:hover span {
                      animation: vibrate 0.8s cubic-bezier(0.36, 0.07, 0.19, 0.97);
                    }

                    @keyframes vibrate {
                      0%, 100% {
                        transform: translateY(0px);
                      }
                      10% {
                        transform: translateY(-2px);
                      }
                      20% {
                        transform: translateY(2px);
                      }
                      30% {
                        transform: translateY(-1.5px);
                      }
                      40% {
                        transform: translateY(1.5px);
                      }
                      50% {
                        transform: translateY(-1px);
                      }
                      60% {
                        transform: translateY(1px);
                      }
                      70% {
                        transform: translateY(-0.5px);
                      }
                      80% {
                        transform: translateY(0.5px);
                      }
                      90% {
                        transform: translateY(-0.25px);
                      }
                    }
                  `}</style>
                </Link>
              );
            })}
          </div>

          <div className="hidden md:flex items-center gap-4">
            <a
              href="#contact"
              className="bg-linear-to-r from-blue-600 to-purple-600 text-white px-10 py-4 rounded-full font-medium hover:shadow-lg transform hover:-translate-y-0.5 transition-all"
            >
              Let's Connect
            </a>
          </div>

          {/* Mobile Menu Button */}
          <div className="md:hidden flex items-center gap-2">
            <button onClick={() => setIsMobileMenuOpen(!isMobileMenuOpen)}>
              {isMobileMenuOpen ? (
                <X className={isScrolled ? "text-gray-900" : "text-white"} />
              ) : (
                <Menu className={isScrolled ? "text-gray-900" : "text-white"} />
              )}
            </button>
          </div>
        </div>

        {/* Mobile Menu */}
        {isMobileMenuOpen && (
          <div className="md:hidden mt-4 bg-white rounded-lg shadow-xl p-4">
            {navLinks.map((link) => (
              <a
                key={link.name}
                href={link.href}
                className="block py-3 text-gray-700 hover:text-blue-600 font-medium border-b border-gray-100 last:border-0"
                onClick={(e) => {
                  e.preventDefault();
                  setActivePath(link.href);
                  setIsMobileMenuOpen(false);
                }}
              >
                {link.name}
              </a>
            ))}
            <a
              href="#contact"
              className="block mt-4 bg-linear-to-r from-blue-600 to-purple-600 text-white px-6 py-3 rounded-full font-medium text-center"
              onClick={() => setIsMobileMenuOpen(false)}
            >
              Get in Touch
            </a>
          </div>
        )}
      </div>
    </nav>
  );
};

export default Header;